.content {
    height: auto;
    margin: 50px auto;

    .category {
        background-color: $base-background;
        text-align: center;
        padding: 5px 30px;
        position: relative;
        z-index: 1000;
        color: $base-color;
        top: -5px;
        font-size: 1.4em; }

    .category-separator {
        width: 100%;
        height: 13px;
        border-bottom: 1px solid $base-color;
        text-align: center; }

    .item {
        margin-top: 30px;
        max-height: 500px;

        .item-title {
            line-height: 1.5em;
            font-weight: 600;
            font-stretch: ultra-condensed; }

        .post-owner {
            max-height: 52px;

            .post-owner-figure {
                max-width: 52px;
                max-height: 52px;
                object-fit: cover;
                border-radius: 50%;
                padding: 2px;
                border: 1px solid lighten($base-color, 25%); }

            .owner-name, .item-date {
                margin: 0; }

            .owner-name {
                margin-top: 4px; }

            .item-date {
                color: lighten($color, 35%);
                font-size: .8em;
                font-weight: 300; } }

        .item-description {
            font-weight: 300; }

        &.item-inline {

            .post-img {
                object-fit: cover;
                min-height: 200px;
                max-height: 200px;
                min-width: 328px;
                width: 100%;
                margin-bottom: 20px; } } } }
